*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    
  }
  
html{
    background-color:  rgb(255, 255, 255);
}
.darkmode{
    filter: invert(1) hue-rotate(180deg);
    transition: .02 ease-in-out;
}
.modnoturne{
    align-items: center;
    display: flex;
    position: absolute;
    top: 5px;
    right: 45%;
}
.modnoturne h4{
    padding-right: 15px;
}
.modnoturne svg{
    transition: 0.2s ease-in-out;
    cursor: pointer;
}
.modnoturne svg:hover{
    transform: scale(1.1);
}
.container{
    height: auto;
    min-height: 100vh;
    display:flex ;
    align-items: center;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
}

