.title{
    font-size: 55px;
    color: rgb(70, 70, 70);
    padding-top: 50px;
}
h4{
    padding-top: 20px;
}
h4 span{
    cursor: pointer;
    transition: 0.2s ease-in-out;
}
h4 span:hover{
   font-size: 24px;
}
@media (max-width: 620px)
{
    .title{
        font-size: 45px;
        
        margin-left: 10%;
    }
}

@media (max-width: 480px)
{
    .title{
        font-size: 40px;
        
        margin-left: 10%;
    }
}

@media (max-width: 445px)
{
    .title{
        font-size: 40px;
        
        margin-left: 10%;
    }
}

@media (max-width: 410px)
{
    .title{
        font-size: 40px;
        margin-left: 10%;
    }
}


.containerInput{
    margin: 15px 0;
    padding: 15px;
    display: flex;
    background-color: transparent;
    border-radius: 8px;
    background-color: rgba(31, 31, 31, 0.2);
}
.containerInput input{
    background-color: transparent;
    border: 0;
    font-size: 20px;
    color: rgb(70, 70, 70);
    outline: none;
    margin-right: 8px;
}
.containerInput input::placeholder,.containerInputUf input::placeholder,
.containerInputCidade input::placeholder
,.containerInputLog input::placeholder{
    color: rgb(70, 70, 70);
}

.containerInputUf,.containerInputCidade
,.containerInputLog{
    margin: 5px 0;
    padding: 15px;
    display: flex;
    background-color: transparent;
    border-radius: 8px;
    background-color: rgba(31, 31, 31, 0.2);
}
.containerInputUf input,.containerInputCidade input
,.containerInputLog input{
    background-color: transparent;
    border: 0;
    font-size: 20px;
    color: rgb(70, 70, 70);
    outline: none;
    margin-right: 8px;
}
.containerInputLog input{
    width: 270px;
}

.buttonSeacrch{
    background-color: transparent;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: transform 0.2s;
    color: black;
}
.buttonReset{
    padding-left: 5px;
    background-color: transparent;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: transform 0.2s;
    color: black;
}
.buttonSeacrch:hover,.buttonReset:hover{
    transform: scale(1.2);
}
.buttonReset:hover{
    transform: scale(1.08);
}

.main{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 80vw;
    max-width: 400px;
    min-width: 360px;
    height: 200px;
    border-radius: 8px;
}



.main h2 {
    margin: 16px 0;
    font-size: 39px;
    color:rgb(70, 70, 70);
}

.main span, h4{
    margin-bottom: 16px;
    font-weight: bold;
    color: rgb(70, 70, 70);
}

#lista{
    height: auto;
    background-color:rgb(255, 255, 255);
    display: block;
    margin-bottom: 50px;
}

#lista li{
    list-style: square;
    color:rgb(70, 70, 70);
    padding-top: 10px;
    font-weight: bold;
}
#lista span,#lista p{
    color:rgb(70, 70, 70); 
    padding-right: 5px;
    padding-bottom: 5px;
}

footer{
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
footer h2{
    margin-bottom: 5px;
    font-size: 12px;
    color: white;
    text-shadow: 2px 2px 8px black;
}
footer a{
    margin-left: 7px;
    color: white;
    text-shadow: 2px 2px 8px black;
}